.btn.default {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  color: #FFFFFF;
  width: 16.75rem;
  height: 2.1rem;
  border-radius: 1.05rem;
  margin: 0 auto;
  margin-top: 1.6rem;
  margin-bottom: 1.6rem;
  background-color: #A678DA;
  opacity: 0.5;
}
.btn.default.active {
  opacity: 1;
}
