.container {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #F7F7F7;
}
.container .addrList {
  flex: 1;
  overflow-y: scroll;
}
.container .addrList .addItem {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 0.5rem;
  margin: 0.5rem 0.5rem 0rem 0.5rem;
}
.container .addrList .addItem .addrBox {
  flex: 1;
  padding: 0.9rem 1.35rem 1rem 0.75rem;
}
.container .addrList .addItem .addrBox .addInfo {
  font-weight: bold;
}
.container .addrList .addItem .addrBox .addInfo .name {
  font-size: 0.7rem;
  color: #333333;
}
.container .addrList .addItem .addrBox .addInfo .tel {
  margin-left: 0.5rem;
  font-size: 0.7rem;
  color: #333333;
}
.container .addrList .addItem .addrBox .addrDesc {
  font-size: 0.6rem;
  color: #969696;
  line-height: 0.85rem;
  margin-top: 0.6rem;
}
.container .addrList .addItem .btnBox {
  width: 2.8rem;
  height: 2.9rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-left: 1px solid #E8E8E8;
}
.container .addrList .addItem .btnBox .btnItem {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 2.8rem;
  height: 1.9rem;
}
.container .addrList .addItem .btnBox .editBtn {
  font-size: 0.6rem;
  color: #B1B1B1;
}
.container .addrList .addItem .btnBox .delBtn {
  font-size: 0.6rem;
  color: #F44F06;
}
