.navContainer {
  position: relative;
  background-color: #F7F7F7;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 2.25rem;
}
.navContainer .goBtn {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  height: 2.25rem;
  padding: 0rem 0.75rem 0rem 0.75rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.navContainer .title {
  font-size: 0.9rem;
  color: #333333;
  line-height: 2.25rem;
  font-weight: bold;
}
.navContainer .rightDom {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
  height: 2.25rem;
  padding: 0rem 0.75rem 0rem 0.75rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
